  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.parchment-card {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.button-medieval {
  width: 100%;
  max-width: 320px;
  margin: 0.7rem auto 0 auto;
  display: block;
}

.input-icon-group.forgot-group {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 320px;
  justify-content: flex-start;
  border: none;
  background: none;
  border-radius: 0;
  margin-bottom: 1.2rem;
  margin-left: auto;
  margin-right: auto;
}

.input-icon {
  display: flex;
  align-items: center;
  height: 44px;
  padding-left: 0;
  margin-right: 0.5em;
  background: none;
}

.forgot-input {
  height: 44px;
  line-height: 44px;
  width: 100%;
  max-width: 320px;
  border: 1.5px solid #a67c52;
  border-radius: 6px;
  background: #fdf8e4;
  font-size: 1.08rem;
  font-family: "MedievalSharp", serif;
  padding: 0 1em;
  box-sizing: border-box;
  text-align: center;
}

.login-links {
  display: flex;
  justify-content: center;
  margin-top: 1.5rem;
  margin-bottom: 1em;
}

.login-links .medieval-link {
  text-decoration: underline;
}

.medieval-msg-btn {
  display: inline;
  margin: 1.2em auto 0 auto;
  padding: 0;
  font-size: 1rem;
  font-family: "MedievalSharp", serif;
  background: none;
  border: none;
  color: #7b5e3b;
  cursor: pointer;
  text-decoration: underline;
  transition: color 0.2s;
  box-shadow: none;
  border-radius: 0;
